.ScreenWalletList {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* delete background color later */
  /* background-color: #393e46; */
}

.addWalletForm {
  position: absolute;
  height: 100vh;
  width: 100vw;
  /* background-color: rgb(0, 0, 0); */
  display: flex;
  /* opacity: 50%; */
  justify-content: center;
  align-items: center;
}

.addWalletForm.hidden {
  display: none;
}

.currentWalletsContainer {
  width: 80vw;
  height: 40vh;
  display: flex;
  position: relative;
  top: 20vh;
  border: none;
  flex-direction: column;
  padding: 25px;
}

.currentWalletsContainerHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 10%;
  width: 100%;
}
.currentWalletsContainerHeader span {
  color: #f3efe0;
  font-size: 18px;
  position: relative;
  bottom: 4px;
}
.currentWalletsContainerHeader p {
  text-decoration: none;
  cursor: pointer;
  border: none;
  /* background-color: none; */
  font-size: large;
  margin-bottom: 10px;
  border-radius: 10px;
  transition: 0.5s;
  position: relative;
  bottom: 20px;
  color: #f3efe0;
}
.currentWalletsContainerHeader p:hover {
  transform: translate(0px, -5px);
  transition: 0.5s;
}

.currentWalletsTable {
  border: solid wheat;
  height: 100%;
  width: 100%;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  overflow: scroll;
}
.currentWalletsTable::-webkit-scrollbar {
  width: 5px;
  text-decoration: none;
}

.add-wallet-button {
  text-decoration: none;
  border: none;
  border-radius: 15px;
  margin-bottom: 7.5px;
}
.add-wallet-button:hover {
  background-color: rgb(175, 174, 174);
}

.wallet-data-header {
  display: flex;
  justify-content: center;
  border-bottom: solid white;
  margin-top: 15px;
  /* align-content: space-evenly; */
}
.wallet-data-point {
  display: flex;
  justify-content: center;
  margin-block: 5px;
}

.wallet-data-point.selected {
  background-color: #22a39f;
}

.formContainer {
  display: flex;
  flex-direction: column;
  padding: 25px;
  padding-inline: 100px;
  align-items: center;
  border-radius: 15px;
  /* border: solid white; */
  box-shadow: 0px 0px 20px white;
  background-color: #22a39f;
  z-index: 1;
}

.formContainerBackground {
  background-color: black;
  opacity: 50%;
  height: 100vh;
  width: 100vw;
  position: absolute;
  z-index: 1;
}

.formInput {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 25vw;
  /* border: solid wheat; */
}

.formInput .btn {
  width: 90%;
  font-size: larger;
  cursor: pointer;
  text-decoration: none;
  border: none;
  background-color: lavender;
  margin: 10px;
  margin-top: 50px;
  border-radius: 10px;
}

.formClose {
  position: relative;
  cursor: pointer;
  left: 65%;
}

.wallet-list-graphs-container {
  display: flex;
  color: white;
  position: absolute;
  bottom: 55vh;
}
